import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from './Card';
import { Button } from './Button';
import { Alert, AlertDescription, AlertTitle } from './Alert';
import './Farms.css';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

const Farms = ({ session }) => {
    const [status, setStatus] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [nftDetails, setNftDetails] = useState([]);
    const [selectedNFTs, setSelectedNFTs] = useState({
        seed: '',
        compost: '',
        wateringCan: '',
    });
    const [plotStatus, setPlotStatus] = useState(null);
    const [wateringCanUses, setWateringCanUses] = useState(null);

    useEffect(() => {
        if (session) {
            fetchNFTs();
            fetchPlotStatus();
        }
    }, [session]);

    useEffect(() => {
        const interval = setInterval(() => {
            if (session) {
                fetchPlotStatus();
            }
        }, 10000);
        return () => clearInterval(interval);
    }, [session]);

    const handleError = (defaultMessage, error) => {
        console.error(defaultMessage, error);
        setStatus(`${defaultMessage}: ${error.message || 'Unknown error occurred'}`);
    };

    const fetchNFTs = async () => {
        if (!session?.actor) {
            setStatus('Error: Please login first');
            return;
        }

        try {
            const response = await fetch(`${API_BASE_URL}/nfts/${session.actor}`);
            const result = await response.json();

            if (Array.isArray(result.nfts) && result.nfts.length > 0) {
                const userNFTs = result.nfts.map(nft => ({
                    templateId: nft.template.template_id,
                    image: `https://ipfs.io/ipfs/${nft.data.img}`,
                    assetId: nft.asset_id,
                    name: nft.name,
                    issued: nft.template_mint,
                    uses: parseInt(nft.data.uses, 10) || 10,
                }));
                setNftDetails(userNFTs);
            } else {
                setNftDetails([]);
                setStatus('No NFTs available');
            }
        } catch (error) {
            handleError('Error fetching NFTs', error);
        }
    };

    const fetchPlotStatus = async () => {
        try {
            const response = await fetch(`${API_BASE_URL}/plotStatus`);
            const result = await response.json();
            setPlotStatus(result);
        } catch (error) {
            handleError('Error fetching plot status', error);
        }
    };

    const handleNFTChange = (type) => (e) => {
        const value = e.target.value;
        const selectedNFT = nftDetails.find(nft => nft.assetId === value);
        setSelectedNFTs(prev => ({
            ...prev,
            [type]: value,
        }));
        if (type === 'wateringCan') {
            setWateringCanUses(selectedNFT ? selectedNFT.uses : null);
        }
    };

    const renderNFTDisplay = (type, label) => {
        const selectedNFT = nftDetails.find(nft => nft.assetId === selectedNFTs[type]);

        return (
            <Card className="nft-display">
                <CardHeader>
                    <CardTitle>{label}</CardTitle>
                </CardHeader>
                <CardContent>
                    {selectedNFT && (
                        <div className="nft-details">
                            <img src={selectedNFT.image} alt={type} className="nft-image" />
                            <div className="nft-info">
                                <p className="font-semibold">{selectedNFT.name}</p>
                                <p className="text-sm text-gray-500">Mint: {selectedNFT.issued}</p>
                                <p className="text-sm text-gray-500">ID: {selectedNFT.assetId}</p>
                            </div>
                        </div>
                    )}
                    <select onChange={handleNFTChange(type)} value={selectedNFTs[type]} className="w-full p-2 border rounded">
                        <option value="" disabled>Select {label}</option>
                        {nftDetails.filter(nft => nft.templateId === type).map(nft => (
                            <option key={nft.assetId} value={nft.assetId}>
                                Issued: {nft.issued}
                            </option>
                        ))}
                    </select>
                </CardContent>
            </Card>
        );
    };

    const isActionDisabled = () => {
        if (isLoading) return true;

        return !selectedNFTs.seed || !selectedNFTs.compost || !selectedNFTs.wateringCan;
    };

    return (
        <div className="farms-section">
            <h2 className="text-2xl font-bold mb-6 text-center">Farms</h2>

            <div className="nft-display-container">
                {renderNFTDisplay('seed', 'Beatz Seeds')}
                {renderNFTDisplay('compost', 'Compost Soil')}
                {renderNFTDisplay('wateringCan', 'Watering Can')}
            </div>

            <Button
                onClick={() => {}}
                disabled={isActionDisabled()}
                className="w-full mb-4"
            >
                {isLoading ? 'Processing...' : 'Perform Action'}
            </Button>

            {status && (
                <Alert variant={status.includes('Error') ? 'destructive' : 'default'}>
                    <AlertTitle>{status.includes('Error') ? 'Error' : 'Success'}</AlertTitle>
                    <AlertDescription>{status}</AlertDescription>
                </Alert>
            )}
        </div>
    );
};

export default Farms;
